'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';

interface Student {
  name: string;
  roll: string;
}

export default function HomePage() {
  const [isLoading, setIsLoading] = useState(true);
  const [student, setStudent] = useState<Student | null>(null);
  const router = useRouter();

  useEffect(() => {
    checkRegistrationStatus();
  }, []);

  const checkRegistrationStatus = async () => {
    try {
      const response = await fetch('/api/student');
      const result = await response.json();

      if (result.registered && result.student) {
        setStudent(result.student);
        // Redirect to QR scanner after a brief delay
        setTimeout(() => {
          router.push('/qr-scanner');
        }, 1500);
      } else {
        // Redirect to registration
        setTimeout(() => {
          router.push('/register');
        }, 1500);
      }
    } catch (error) {
      console.error('Error checking registration status:', error);
      // Default to registration page on error
      setTimeout(() => {
        router.push('/register');
      }, 2000);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-background flex items-center justify-center p-4">
      <Card className="w-full max-w-md">
        <CardHeader className="text-center">
          <CardTitle className="text-2xl font-bold">Attendance System</CardTitle>
          <CardDescription>
            QR Code Based Attendance Tracking
          </CardDescription>
        </CardHeader>
        <CardContent className="text-center">
          {isLoading ? (
            <div className="space-y-4">
              <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto"></div>
              <p className="text-muted-foreground">Checking registration status...</p>
            </div>
          ) : student ? (
            <div className="space-y-4">
              <div className="p-4 bg-muted rounded-lg">
                <p className="font-medium">Welcome back!</p>
                <p className="text-sm text-muted-foreground">Name: {student.name}</p>
                <p className="text-sm text-muted-foreground">Roll: {student.roll}</p>
              </div>
              <p className="text-sm text-muted-foreground">
                Redirecting to QR scanner...
              </p>
            </div>
          ) : (
            <div className="space-y-4">
              <p className="text-muted-foreground">
                No registration found. Redirecting to registration...
              </p>
            </div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
